    #t_1_1.h4.mt-3 1.1 Decreto 1571 de 1993
    .titulo-segundo
    
    p.mb-5 El Decreto 1571 de 1993 es la norma que rige a los bancos de sangre y servicios de transfusin en Colombia, y ha definido al donante de sangre como:


    .row.mb-5.justify-content-center.align-items-center
      .col-12.col-md-8.col-xl-6
        .bloque-texto-b.p-4 
          .bloque-texto-b__texto.h5.mb-0 Persona que, previo cumplimiento de los requisitos sealados en este Decreto, da, sin retribucin econmica y a ttulo gratuito y para fines preventivos, teraputicos, de diagnstico o de investigacin, una porcin de su sangre en forma voluntaria, libre y consciente.
          p.z-index Ministerio de Salud, 
            br
            |2020, p.2

    p.mt-5 En concordancia, si una persona manifiesta su intencin de donar sangre, solo cumple con esta definicin cuando lo hace sin ningn tipo de presin o coaccin, siendo consciente de que sus conductas de vida permitirn salvar o mejorar la calidad de vida de quienes requieren de una transfusin. Es por esto que #[strong el promotor de la donacin de sangre es el directamente responsable de la calidad de la informacin que les brinda a los ciudadanos para que dicha definicin se cumpla], de lo contrario estara en contra de lo establecido en la normatividad, vulnerando la seguridad de los pacientes, entre los cuales se encuentran nios que apenas estn comenzando a vivir. 
    
    
    .row.mt-4
      .col-lg
        p.mb-4 As mismo, este Decreto define a un #[strong banco de sangre] como:
        p.mb-4 #[strong "Todo establecimiento o dependencia con Licencia Sanitaria de Funcionamiento para adelantar actividades relacionadas con la obtencin, procesamiento y almacenamiento de sangre humana destinada a la transfusin de la sangre total o en componentes separados, a procedimientos de afresis y a otros procedimientos preventivos, teraputicos y de investigacin. Tiene como uno de sus propsitos asegurar la calidad de la sangre y sus derivados.]
        p Ministerio de Salud, 2020, p. 1
      .col-lg-4
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_2.png", alt="alt", style="width: 200px; display:block; margin:0 auto;")       

    p.mt-4 De acuerdo con lo anterior, las acciones realizadas por los bancos de sangre <b>deben estar centradas en reducir el riesgo de transmitir </b>infecciones a travs de la transfusin, aunque da a da se requieren fortalecer las pruebas de tamizaje utilizadas para la deteccin de agentes infecciosos en los bancos de sangre (VIH, hepatitis B, hepatitis C, Chagas, sfilis, HTLV, core), el rea de promocin debe ser una prioridad tcnica para los actores que hacen parte de la medicina transfusional, ya que esta determina en buena medida si la sangre proviene de un donante de alto o bajo riesgo, adems de la disponibilidad de dicho suministro.
    br
    p.mt-2 Igualmente, el artculo 4 del Decreto 1571 de 1993 manifiesta que:

    
    .row.mb-5.justify-content-center.align-items-center.mt-3
      .col-lg-6.mb-5.mb-lg-0
        .bloque-texto-b.p-5
          .bloque-texto-b__texto.h5.mb-0 La sangre humana solo podr ser extrada y utilizada sin nimo de lucro, con fines preventivos, teraputicos, de diagnstico en seres humanos o para investigaciones cientficas.
          br
          span.z-index Ministerio de Salud,<br> 2020, p. 3

    p.mt-4 El artculo 28 del Decreto 1571 de 1993, expresa que: <b>Por ser la salud un bien de inters pblico, donar sangre es un deber de solidaridad social que tienen las personas y, por ningn motivo, podr ser remunerado</b> as mismo, se sentencia que <b>el acto de donacin sea consciente, expreso y voluntario por parte del donante</b> (Ministerio de Salud, 2020, p. 10). Esto ratifica que, la sangre donada con fines transfusionales solo pueda estar enmarcada bajo aspectos de tipo social, voluntarios y altruistas. 
    
    .cajon.cajon--gris.p-4.mb-5.mb-lg-0.mt-5    
      p.mb-0 Por lo tanto, quienes ejerzan funciones de promocin de la donacin de sangre, deben informar adecuadamente a las personas, <b>utilizando mensajes claros, precisos, basados en conceptos tcnicos que conlleven a la seguridad transfusional,</b> y que estos se den en ambientes que les permita tomar una decisin voluntaria, consciente, madura y responsable sobre el acto de donar.

    #t_1_2.h4.mt-5 1.2 Resolucin 0901 de 1996
    .titulo-segundo

    p.mt-3 El Manual de normas tcnicas, administrativas y de procedimientos en bancos de sangre, ha sido publicado a travs de la Resolucin 0901 de 1996 (Ministerio de Salud, 2020). Tiene como objetivos especficos,<b> proteger tanto a los donantes como a los receptores de sangre, crear y mantener la garanta de la calidad de la sangre y sus hemocomponentes.</b>

    .row.justify-content-center.align-items-center.mt-4
      .col-auto.me-5
        p.mb-3 En el captulo 3 de dicha resolucin, se expresa que:
        span.etiqueta--roja.mb-4 <b>Donar sangre es un deber y un #[br]derecho de la solidaridad social que #[br] tienen las personas</b> 
        br
        br       
        p (Ministerio de Salud, 2020, p. 10).
      .col-auto 
        img.img-a.my-3(src="@/assets/curso/images/pages/ilustraciones/img_3.png").img-fluid

  
    p.mt-4 La anterior afirmacin que da a conocer que es un deber social el ejercer la donacin de sangre, pero, as mismo, expresa que los donantes son sujetos de derechos. Tambin se determina que: 

    .bloque-texto-a.p-4.p-md-5.mt-5
      .row.m-0.align-items-center.justify-content-between
        .col-lg-4.mb-4.mb-lg-0
