        .col-lg-7
          .bloque-texto-a__texto.p-4
            p Todos los donantes potenciales deben recibir materiales educativos y tener la posibilidad de leer carteles o mensajes, referentes a los riesgos de enfermedades transmisibles por transfusin, con el fin de <b>darles la opcin de autoexcluirse de donar o de evitar que la unidad recolectada sea utilizada con fines transfusionales (Ministerio de Salud, 2020, p. 10).</b>

    p.my-5 Por ello es responsabilidad de los bancos de sangre <b> contar con la logstica y herramientas necesarias para que los donantes potenciales sean bien informados,</b> y que la invitacin a donar no est solo centrada en la edad y peso mnimo requerido, puesto que para cumplir el objetivo de reducir el riesgo transfusional se debe ir ms all, es decir, abordar aspectos relacionados con las conductas de vida que conllevaran a que una persona tenga una alta probabilidad de estar infectada y que si acude a donar, podra estar transmitiendo dichas infecciones a otros que apenas estn empezando a vivir o que tienen todas sus esperanzas de vida en la transfusin. <b>Es fundamental que el promotor conozca, sepa explicar y le d a conocer a cada donante abordado, el concepto de ventana inmunolgica.</b>

    .crd_A.crd_A--azul
      .row.justify-content-center.align-items-center.mt-3
        .col-auto.mb-4.mb-xl-0
            img.img-a.my-3(src="@/assets/curso/images/pages/ilustraciones/img_4.png").img-fluid
        .col-12.col-xl
          p.text-jumbotron-padding As, el promotor informa adecuadamente y se le permite al donante revisar la posibilidad de autoexcluirse en caso de considerar tener un <b>factor de riesgo que podra afectar al o los pacientes que pudiesen ser transfundidos con la sangre donada,</b> y que adems perciba la autoexclusin como una accin de responsabilidad social. 

          p.text-jumbotron-padding As mismo, al darle a conocer al donante los cuidados que debe tener luego de la donacin, <b>se le da a conocer que su bienestar es vital para los bancos de sangre,</b> lo cual genera confianza en la poblacin.
    
