    .h5.mt-5 1.3.1. Circular 001 de 2006          

    p.mb-4 Debido a que la donacin de sangre se debe realizar bajo condiciones de tipo social, voluntaria y no remunerada, #[strong la Circular 001 de 2006 emitida por el Instituto Nacional de Salud], le ratifica a los actores de la red lo expresado en el Decreto 1571 de 1993 y la Resolucin 0901 de 1996, enunciando que la donacin de sangre se debe dar bajo acciones sociales, que #[strong dicha decisin debe ser libre, consciente y no coaccionada (INS, 2020).]

    .row.justify-content-center.align-items-center.mb-5
      .col-auto     
        figure
