    .cajon.cajon--gris.p-4.mb-4         
      p Adems, manifiesta que <b> la donacin coaccionada o de reposicin, va en contra de la cultura de la donacin </b> y, por lo tanto, no se debe ofrecer nada a cambio, como por ejemplo, el presionar o exigir donantes a los familiares y amigos de los pacientes para la prestacin de servicios de salud (INS, 2020).
      

    p.mt-3 As mismo, esta circular aclara que el carn de donante de sangre <b> no es un seguro para poder acceder a obtener gratuitamente hemocomponentes en caso de que sean requeridos por el donante o alguno de sus familiares;</b> se concibe como el soporte que evidencia que dicha persona es solidaria, adems tiene como propsito recordarle la fecha de la prxima donacin. Finalmente, se puntualiza que <b> se deben adoptar medidas para que las personas no sigan concibiendo la donacin como la obtencin de un beneficio tangible</b> (INS, 2020).

