    p.mb-5 Adems, en la circular se manifiesta que un banco de sangre <b> no puede realizar jornadas de donacin en un departamento diferente a su rea de influencia.</b> Para poder ingresar, debe gestionar un permiso con la direccin territorial, es decir, con la secretara de salud departamental, en el cual se debe comprometer con dicha institucin, en que la sangre que sea colectada ser distribuida en los servicios de transfusin de ese departamento. La secretara de salud tiene la autonoma de decidir si le permite o no el ingreso,<b>si no lo permite, el banco de sangre no podra realizar dicha jornada de donacin.</b> Lo anterior aplica si es para una jornada mvil o para un puesto fijo de colecta.

