    h5 Coordinacin Red Nacional Bancos de Sangre y Servicios Transfusionales  INS
    br
    p Evaluar los criterios de organizacin y planificacin de la Red cuando se presenten solicitudes de apertura de un puesto de captacin (fijo o mvil) o un nuevo banco de sangre, emitir un Concepto de viabilidad que podr ser favorable o no: 
    br
    p.mb-5 En los dos casos se informar al Invima, a la SSD quien ejerce la coordinacin territorial, de la Red de Sangre y al solicitante. #[strong Para la expedicin de dicho concepto se deben revisar los criterios planteados por las secretaras de salud departamentales] quien ejerce la coordinacin territorial de la Red de Sangre. 

    .row.mb-5
