      .col-12.col-lg
        p Para los casos en los que el concepto sea favorable, <b>la Coordinacin de la Red Nacional de Bancos de Sangre proceder a otorgar el Cdigo Nacional del Banco de Sangre </b> y su respectiva inscripcin en la Red Nacional de Bancos de Sangre, con base en el concepto que emita el Invima como autoridad sanitaria de Inspeccin Vigilancia y Control, respecto al cumplimiento de requisitos de acuerdo con la normatividad vigente que rigen para bancos de sangre. Cumplido este trmite, el Banco de Sangre podr iniciar sus actividades. El Invima realizar visita de seguimiento peridico posterior al inicio de las actividades del banco de sangre (INS, 2020).

    .h5.mb-4 Circular 003 de 2015

    p.mb-5 En el 2015 el Instituto Nacional de Salud emite oficialmente la Circular 003, la cual tiene como propsito <b> dar a conocer algunas recomendaciones a los actores de la red para prevenir o mitigar la deficiencia de hemocomponentes en temporada decembrina y vacacional, </b> ya que, debido a las celebraciones y actividades relacionadas con la poca, se ha evidenciado una baja en la colecta de sangre, lo cual puede impactar en la seguridad de los pacientes (INS, 2020). 

