    p.mb-5 Los actores de la red deben prever aquella poca en la que la deficiencia de hemocomponentes se hace ms evidente. Por lo tanto, debern crear estrategias educativas, a travs de las cuales, se den a conocer a la poblacin,<b> la importancia de donar sangre de manera voluntaria y frecuente para poder contar con un suministro oportuno de hemocomponentes, </b> pero, sobre todo, de bajo riesgo. El mensaje dado no debe ser alarmante y siempre basado en aspectos positivos.     
    
    #t_1_4.titulo-segundo
      .h4 1.4 Lineamientos   

    p.mb-4 Dada la necesidad de fortalecer los aspectos tcnicos, la Coordinacin de la Red Nacional de Bancos de Sangre y Servicios de Transfusin, en cabeza del Instituto Nacional de Salud, <b> tambin ha dado lugar a la generacin y publicacin de documentos denominados como lineamientos,</b> en los cuales se dan a conocer directrices especficas y que estn asociadas a la promocin de la donacin de sangre.

    .cajon.cajon--gris.p-4.mb-5
      p.mb-0 El contenido de estos documentos es considerado importante para la <b>Red de Sangre en Colombia,</b> en aras del fortalecimiento de la seguridad transfusional, razn por la cual, <b>quienes realicen actividades de promocin deben conocerlas e incentivar su implementacin.</b>

    .row.justify-content-center.align-items-center.mb-5
      .col-auto 
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_10.png", alt="alt", style="width:1500px;")        
        

    .h5 Lineamiento Promocin de la donacin de sangre y componentes anatmicos

    p.mt-3 La promocin de la donacin de sangre ha sido catalogada como el pilar fundamental de la seguridad transfusional, ya que, de la gestin realizada por los encargados de desempear esta labor, se determina el riesgo de transmisin de infecciones a travs de la transfusin y la disponibilidad de los hemocomponentes (INS, 2020). 
    br
    p De acuerdo con la contribucin en salud pblica que tiene la promocin de la donacin de sangre, el presente lineamiento expresa las consideraciones que debe evaluar e implementar el promotor, la poblacin general y quienes ejercen funciones de comunicacin; lo cual quiere decir que dicho documento tiene un enfoque transversal.
    br
    p #[strong Las acciones descritas en este documento se encaminan hacia los derechos humanos, ya que debe existir una coherencia entre lo que se desea recibir y lo que se desea para los dems.] Dichas acciones permiten que los promotores de la donacin de sangre perciban que el donante tambin es un sujeto de derechos, ya que los derechos humanos amparan a todas las personas sin ningn tipo de distincin (INS, 2020). 
    br    
    p Por otro lado, los derechos humanos son interdependientes, por ejemplo, <b>un donante tiene derecho a ser informado sobre conductas de autocuidado, la importancia de donar sangre de manera frecuente, cuidados antes durante y posteriormente a haber donado,</b> ese tipo de informacin le aporta para poder ejercer su derecho a la salud y a la vida (INS, 2020).
    br
    p.mb-5 De la misma manera, los derechos humanos no se pierden con el tiempo, por ejemplo, no es posible que hoy se informe adecuadamente al donante pero ya maana no, as que su derecho a estar informado debe permanecer en el tiempo.   

    .row.justify-content-center.align-items-center.mb-5
      .col-auto           
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_11.png", alt="alt", style="width:1500px;")        
      
    p.mb-5 Las consideraciones tcnicas del lineamiento tambin estn basadas en los principios ticos, por ejemplo, <b> los donantes de sangre por su accin social de salvar vidas deben ser valorados, cuando esto sucede, se cumple con el principio tico de la dignidad.</b> As mismo, con la informacin previa que una persona recibe sobre la donacin de sangre, puede decidir si dona o no lo hace, esta disposicin <b> debe ser respetada y libre de cualquier tipo de coaccin, </b> para que as no se le est vulnerando el principio tico de la autonoma (INS, 2020).

    .crd_A.crd_A--azul.mb-5    
      .row.justify-content-center.align-items-center
        .col
          p Cuando una persona dona sangre con la conviccin de que su sangre es de bajo riesgo de transmitir cualquier tipo de infeccin, y por consiguiente, los hemocomponentes obtenidos van a beneficiar a los pacientes a los cuales les sern transfundidos, <b>se ejerce el principio tico de la beneficencia.</b> Si un donante de sangre concibe <b>la autoexclusin como un acto de responsabilidad social, se hace alusin al principio tico de no maleficencia.</b>
        .col-auto
          img.number(src='@/assets/curso/images/pages/ilustraciones/ico_12.svg' alt="", style="width:150px; display:block; margin:auto 0;").img-float

    .h5.mb-4 Lineamientos Seleccin de donantes de sangre

    p.mt-3 Los encargados de realizar la promocin de la donacin de sangre, tienen la responsabilidad de <b> informar a los donantes sobre los requisitos que deben cumplir para hacer efectiva su donacin,</b> esto previamente al diligenciamiento de la encuesta y entrevista. Por esta razn, deben conocer los criterios establecidos, adems de su fundamentacin, especialmente centrndose en aquellos aspectos que son de un mayor inters para la poblacin, como por ejemplo: 

    .tarjeta--container.row.mb-5.mt-5
      .col-md.tarjeta.tarjeta--roja.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/images/pages/ilustraciones/ico_13.svg', alt='Texto que describa la imagen')
          
        .h3.text-center Una persona que tenga tatuajes puede donar sangre? 
        p Respuesta: s puede ser donante de sangre, posterior a los 12 meses despus de haberse realizado el tatuaje. 
        br
        p Debido a que el personal del banco de sangre desconoce las condiciones de asepsia en las que el tatuaje fue realizado, se debe esperar dicho tiempo, ya que si la persona fue infectada, al comienzo de la enfermedad los niveles de la infeccin no son los suficientes para que sean detectados por las pruebas infecciosas realizadas por los bancos de sangre, esto a lo que se le conoce como periodo de ventana inmunolgica. 

      .col-md.tarjeta.tarjeta--roja-1.p-5
        .row.justify-content-center.mb-4
          .col-6
            figure
              img(src='@/assets/curso/images/pages/ilustraciones/ico_14.svg' alt="", style="width:250px; display:block; margin:auto 0;").img-float
          
        .h3.text-center Una persona que haya tenido hepatitis?
        br
        p Respuesta: se presume que la actividad sexual inicia a los 12 aos, si un donante potencial refiere haber tenido hepatitis antes de los 12 aos, esta persona no tiene restriccin para donar por este criterio, ya que seguramente a dicha edad no haba iniciado su actividad sexual y, por lo tanto, lo ms seguro es que haya tenido hepatitis A por contaminacin de alimentos, por el contrario, si refiere haber tenido hepatitis despus de los 12 aos, dicho donante no se puede aceptar porque pudo haber sido una hepatitis A por contaminacin de alimentos pero tambin pudo haber sido una hepatitis B por contacto sexual (INS, 2020):

      .cajon.cajon--gris.p-4.mb-5.mb-lg-0.mt-5
        p.mb-0 En Colombia los criterios de seleccin de donantes <b> de sangre se encuentran unificados bajo un lineamiento emitido por el Instituto Nacional de Salud.</b>  

    .titulo-segundo
      #t_1_5.h4 1.5 Boletines tcnicos    
    
    p.mb-5 La red de sangre emite peridicamente boletines tcnicos, los cuales tienen como propsito <b> actualizar a los actores de la red en los diferentes aspectos de la medicina transfusional.</b> Teniendo en cuenta la necesidad de fortalecer las capacidades tcnicas de quienes ejercen la labor de promover la donacin de sangre; a continuacin, se revisar lo argumentado en dichos documentos.

    .h5.mb-4 La promocin como impacto en la seguridad transfusional 

    p Este lineamiento tiene como finalidad manifestar a los actores de la red que la promocin de la donacin tiene una relevancia tcnica en la seguridad transfusional y que, por lo tanto, se requiere de la generacin de acciones para su fortalecimiento. Es por esto que el Instituto Nacional de Salud da a conocer a los actores de la red las recomendaciones a tener en cuenta en la promocin de la donacin voluntaria y habitual de sangre, para lo cual los bancos de sangre debern tener en cuenta lo siguiente:

    hr.mt-5
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico1.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float    
      .col
        p La promocin se debe realizar con el propsito de captar donantes habituales: el donante habitual es aquella persona que dona dos veces en doce meses, aunque la frecuencia de donacin es importante, no se puede dejar a un lado que esta persona, adems, debe ser consciente de que sus conductas de vida no generan riesgo alguno en quien reciba la sangre donada. as as
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico2.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float     
      .col
        p Contar con personal capacitado y con experiencia en promocin y colecta, que emplee un lenguaje sencillo en el abordaje al donante, de la misma manera como se evidencia previamente que una persona est capacitada para ejercer labores en inmunohematologa, inmunosoderologa, etc., el director del banco de sangre al ser responsable de todos los actos tcnicos, cientficos y administrativos, debe verificar que quien realice funciones de promocin est debidamente entrenado y capacitado, antes de salir a trabajo de campo. 
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico3.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float   
      .col
        p Contar con los insumos y la logstica necesaria para la realizacin de colectas de sangre: el grupo de promocin debe contar con material ldico de apoyo, para facilitar la emisin del mensaje.
    hr
    .row.align-items-center.mt-3
      .col-auto
        figur
          img(src='@/assets/curso/images/pages/ilustraciones/ico_31.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float   
      .col
        p Garantizar que tanto en las campaas como en el banco de sangre se brinde la misma informacin al donante potencial.
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_32.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float     
      .col
        p Tener procedimientos y registros documentados: el rea de promocin debe contar con un procedimiento en el cual se estandaricen las actividades a desarrollar, de tal manera que quienes ejerzan dicha labor lo hagan de la misma manera, as mismo se deben generar <b> registros que permitan evidenciar la trazabilidad de las acciones ejecutadas,</b> adems, dicha informacin servir para la toma de decisiones.  
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_33.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float        
      .col
        p Evaluar de manera sistemtica mediante verificacin y auditoras los procedimientos y la gestin en promocin y colecta: se requiere la realizacin de auditoras con el fin de identificar aquellos aspectos que requieren acciones de mejora.  
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_34.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float     
      .col
        p Realizar seguimiento permanente al indicador de donantes voluntarios y habituales de sangre, as como a la positividad o casos de VIH, hepatitis por cada 10.000 donaciones de sangre y por tipo de donante; todo promotor debe basar su actuar en una revisin y anlisis previo de los indicadores.
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_35.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float        
      .col
        p Otorgar el carn de donante de sangre que tiene como nico fin confirmar el grupo sanguneo, recordacin de la prxima donacin y reconocimiento social..  
    hr
    .row.align-items-center.mt-3
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_36.svg' alt="", style="width:60px; display:block; margin:auto 0;").img-float     
      .col
        p Contar con clubes de donantes de grupos sanguneos poco frecuentes: esto permite contar con donantes frecuentemente informados y dispuestos a donar cuando sea necesario.
    hr

    .h5.mt-5 Resumen casos de donacin coaccionada 2016 (INS, 2020)
      p.mt-3 La donacin coaccionada, familiar o de reposicin ha sido considerada como de <b> alto riesgo para la transmisin de infecciones a los pacientes a travs de la transfusin,</b> pues a dichos donantes se les invita a donar sin brindarles la informacin previa necesaria y bajo condiciones como las que se describen a continuacin:
    
    SlyderC.mb-5.mt-5(:datos="datosSlyderC")

    .crd_A.crd_A--azul.mb-5  
      .row.justify-content-center.align-items-center
        .col-auto
          img.img-jumbotron-padding(src='@/assets/curso/images/pages/ilustraciones/ico_17.svg' alt="", style="width:150px; display:block; margin:auto 0;")
        .col
          p Este boletn tcnico tiene como propsito definir el algoritmo de accin cuando son notificados ante la <b> Red Nacional de Bancos de Sangre y Servicios de Transfusin del Instituto Nacional de Salud,</b> posibles casos de donacin de sangre coaccionada y, a su vez, con lo identificado <b>retroalimentar a los actores de la red sobre este tipo de situaciones presentadas, </b> para evitar que se vuelvan a presentar.

    .h5 Mecanismos de respuesta de la Red de Sangre en emergencias 

    p.mt-5 El Instituto Nacional de Salud (INS) establece los lineamientos para <b> optimizar los mecanismos de respuesta de la Red Nacional de Bancos de Sangre y Servicios de Transfusin ante situaciones crticas de desabastecimiento de sangre y hemocomponentes </b> a nivel nacional, departamental y local (desastres naturales, eventos pblicos que concentran gran nmero de personas, situacin social de conflicto que pueda generar alto nmero de pacientes, entre otros); <b>protegiendo la sangre como un bien de inters pblico social y garantizando el fortalecimiento de la seguridad transfusional</b> bajo los principios de acceso, equidad, solidaridad y seguridad.

    p.mt-3 Por lo tanto, establece las actividades que deben realizar los actores de la red para responder efectivamente ante una alerta ocasionada por riesgo de desastre, emergencia o desastre, entre las cuales estn:

    TabsB.my-5
      .py-4.py-md-5(titulo="Ministerio de Salud y Proteccin Socia") 
        p.mb-0 El Centro Regulador de Urgencias y Emergencias (CRUE) Nacional se considera como la fuente oficial para generar alertas con el desabastecimiento de hemocomponentes.
      .py-4.py-md-5(titulo="Instituto Nacional de Salud")        
        p.mb-0 Responsable de monitorear el comportamiento de los bancos de sangre. As mismo debe identificar la disponibilidad de hemocomponentes en los bancos de sangre priorizados para generar respuesta (por tamao, o cercana geogrfica), a travs del Sistema de informacin en Hemovigilancia Sihevi-INS. Tambin es el encargado de establecer los mecanismos de comunicacin con los bancos de sangre que prestaran el apoyo requerido.
      .py-4.py-md-5(titulo="Direcciones territoriales")        
        p.mb-0 Monitorear de manera constante el comportamiento de demanda y abastecimiento de hemocomponentes en su rea de influencia, a travs del Sistema de informacin en Hemovigilancia Sihevi-INS. As mismo, identificar los bancos de sangre proveedores en cada una de las IPS que transfunden.
      .py-4.py-md-5(titulo="Bancos de Sangre y Servicios de Transfusin")        
        p.mb-0 Mantener actualizada su informacin en Sihevi-INS respecto a captacin, transfusin, satisfaccin a la demanda, proveedores de hemocomponentes, disponibilidad de sangre y dems variables que ataen a su gestin, ya que es la fuente oficial para monitorear la capacidad de respuesta desde el INS, como Coordinador Nacional de la Red.
        

    p.mb-4 Tambin deben contar con un procedimiento para establecer los inventarios de hemocomponentes, de acuerdo con sus necesidades diarias, procurando adems contar con un excedente en el suministro que logre atender las necesidades que se presenten durante tres (3) das, como mnimo. Este criterio requiere de una estricta labor por parte del grupo de colecta, en donde se encuentra incluido el grupo de promocin.   

    .row.justify-content-center.align-items-center.mb-5
      .col-auto 
        figure
          img(src="@/assets/curso/images/pages/ilustraciones/img_17.png", alt="alt", style="width:1200px;")

    .h5.mb-5 Flujo de comunicacin para cubrir el desabastecimiento de los hemocomponentes

    hr
    .row.align-items-center
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_5.svg' alt="", style="width:70px; display:block; margin:auto 0;")
      .col
        p #[strong 1) El Centro Regulador de Urgencias y Emergencias Nacional  CRUE,]  alertar al INS a travs del Centro Regulador de Trasplantes  CRT, sobre la preparacin para la respuesta o activacin del abastecimiento de sangre y hemocomponentes en casos de riesgo de desastre, emergencia o desastre. 
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_19.svg' alt="", style="width:70px; display:block; margin:auto 0;").img-float    
      .col
        p #[strong 2) El INS en su rol de autoridad nacional y vocero oficial debe generar alerta a los bancos de sangre, iniciando con los que reportan una captacin superior a 12.000 unidades al ao,] junto a aquellos que estn cercanos a la zona del evento (riesgo de desastre, emergencia o desastre), con el fin de que verifiquen su inventario de sangre y hemocomponentes, lo racionen y estn preparados para apoyar la movilizacin de los mismos en caso de que se requiera. 
    hr
    .row.align-items-center
      .col-auto
        figure
          img(src='@/assets/curso/images/pages/ilustraciones/ico_20.svg' alt="", style="width:70px; display:block; margin:auto 0;").img-float   
      .col
        p #[strong 3) El INS ser el vocero oficial ante el Ministerio de Salud y Proteccin Social] y los actores de la red de sangre para reportar la situacin de disponibilidad y la oportunidad en la respuesta ante necesidades de hemocomponentes en situaciones de riesgo de desastre, emergencia o desastre en el territorio nacional
    hr.mb-5

    .h5 Colecta de Sangre en Puestos Mviles (INS, 2020)

    p.mb-5 El presente documento fue publicado por el Instituto Nacional de Salud, con el fin de que los <b> bancos de sangre conozcan los pasos para organizar y desarrollar la colecta de sangre </b> y dando a conocer algunas acciones a tener en cuenta antes, durante y despus de la colecta de sangre en los puestos mviles, esto con el fin de fortalecer la seguridad transfusional.

    .h6.mb-4 #[strong 01. Previamente a la jornada de donacin]

    .crd_A.crd_A--gris.brd-r1.mb-5
      .row.justify-content-center.align-items-center
        .col-auto.mb-5
          img(src='@/assets/curso/images/pages/ilustraciones/img_18.svg' alt="", style="width:500px; display:block; margin:auto 0;")
        
      .h6.mb-4 #[strong Revisin de indicadores y gestin de inventarios:]           
              
      p Este tipo de informacin le permite al promotor tomar decisiones adecuadas sobre el nmero de unidades a colectar y as definir las jornadas a realizar. Tambin permite que el promotor pueda <b> observar a travs del tipo de donante captado y de las tasas de positividad, si realmente las estrategias educativas implementadas estn conllevando a la captacin donantes </b> con una probabilidad baja de tener alguna infeccin.
      hr.my-5

      .h6 Gestin de donantes             
      p Es importante que el promotor realice las jornadas de donacin en poblaciones consideradas con bajo riesgo de infeccin. 
      br
      p As mismo, <b>las personas deben ser debidamente informadas para que puedan tomar una decisin consciente, responsable y madura de donar su sangre,</b> por lo tanto, los promotores siempre deben contar con estrategias que conlleven a la cultura de donacin.
      br
      p El grupo de promocin debe estar atento a realizar actividades que permitan convocar a donar a quienes ya lo han hecho previamente.
      hr.my-5

      .h6 Talento humano:           
      p Estimar el nmero de personas designadas para cada jornada de donacin.
      hr.my-5
      
      .h6 Infraestructura:
      p Gestionar los trmites necesarios para adquirir las autorizaciones de ubicacin del equipamiento y grupo de colecta de sangre.
        br
        br
        | Las reas que vayan a ser utilizadas para diligenciamiento de la encuesta, valoracin clnica y entrevista deben cumplir con condiciones de privacidad, es decir, que sean individuales e independientes en donde el donante perciba un ambiente de confidencialidad. 
        br
        br
        | Tener en cuenta las caractersticas de ventilacin y control de temperatura adecuados en el que se va a ubicar el puesto mvil, <b> rango que estar entre 15 C a 24C, de acuerdo con el clima de la zona del pas en donde se est realizando la colecta de sangre,</b> para as controlar las condiciones de temperatura y evitar eventos adversos. Para el cumplimiento de dichos parmetros, dentro del equipamiento de la colecta mvil de sangre, se contar con dispositivos que permitan la regulacin de la temperatura ambiente.
      hr.my-5

      .h6 Sistema de Gestin de Calidad:          
           
      p Diligenciar todos los documentos relacionados con la realizacin de la campaa y anlisis posterior a la misma. As mismo, #[strong realizar consulta sistemtica y oportuna de manuales, procedimientos e instructivos relacionados con la colecta de sangre en puestos mviles.]
        br
        br
        | Los promotores deben contar con procesos de induccin y capacitacin sistemtica.
      hr.my-5

      .h6 Equipos, materiales e insumos:
      p Segn sea el caso, se debe realizar el alistamiento de: carpas o unidad mvil, camillas, equipos electrnicos, ya sean computadores, tabletas o celulares, puntos de acceso a internet, equipos de refrigeracin, papelera, material informativo sobre la donacin, insumos necesarios para la colecta y mantenimiento de la sangre en condiciones ptimas de conservacin.

        
    .h6.mb-5 #[strong 02. Durante la jornada de donacin]

    .crd_A.crd_A--gris.brd-r1.mb-5    
      .row.justify-content-center.align-items-center
        .col-auto.mb-4
          img(src='@/assets/curso/images/pages/ilustraciones/img_19.png' alt="", style="width:500px; display:block; margin:auto 0rem;")
        
          
      h6.mt-5 #[strong Talento humano:]
      p El personal que realice la promocin #[strong debe estar debidamente entrenado y con la capacidad para emitir respuestas], segn las necesidades de informacin de los donantes.
      hr.my-5
      
      .h6 Sistema de Gestin de Calidad:
      p Documentar dentro de los procesos de captacin, seleccin y donacin, los procedimientos y actividades realizadas. Registrar cada actividad en los formatos establecidos, <b>de tal manera que se pueda observar la trazabilidad del proceso de promocin.</b>  
      
      hr.my-5

      .h6 #[strong Infraestructura:]
      p En las colectas mviles <b>se debe primar porque las condiciones de infraestructura sean similares a las de la sede central del banco de sangre, as como las condiciones de limpieza, aseo y desinfeccin.</b>
      hr.my-5
      
      .h6 #[strong Materiales y equipos:]
      p Disponer de recursos y material ldico para informar, sensibilizar y educar a los donantes de sangre, profundizando en conceptos que les permitan autoexcluirse al considerar que sus conductas de vida son de alto riesgo para transmitir infecciones a travs de la sangre. 
        br
        br
        | Para la extraccin de sangre en climas clasificados como clidos (temperatura superior a 23 C) y templados (temperatura 15 C y 23C), <b> se debe disponer de dispositivos que regulen la temperatura ambiente en un rango entre 15 C y 24 C,</b> con el fin de disminuir el riesgo de Reacciones Adversas a la Donacin (RAD).
        
    
    .h6.mb-5 #[strong 03. Posteriormente a la jornada de donacin]

    .crd_A.crd_A--gris.brd-r1.mb-5    
      .row.justify-content-center.align-items-center
        .col-auto.mt-2
          img(src='@/assets/curso/images/pages/ilustraciones/img_20.png' alt="", style="width:500px; display:block; margin:auto 0;")
        
      .h6.mt-5 Talento humano:
      p Se deben establecer mecanismos que permitan <b> evidenciar los conocimientos de quienes ejercen funciones de promocin.</b> Adems de realizar jornadas de reinduccin.
      hr.my-5

      .h6 Sistema Gestin de Calidad y Revisin de indicadores:
      p Realizar anlisis de indicadores (% donante habitual, tasas de positividad, entre otros), para establecer: acciones de mejora y proyeccin de metas.
        br
        br
        |  Determinar las causas y realizar anlisis y seguimiento de las RAD presentadas. Llevar a cabo ejercicios de trazabilidad que permitan #[strong verificar el cumplimiento de los lineamientos, procedimientos y actividades relacionadas con los procesos de promocin, seleccin de donantes y colecta], entre otros. Adems, realizar anlisis de encuestas de satisfaccin e implementacin de acciones de mejora. 
        br
        br
        |  Se deben realizar anlisis de las situaciones que se puedan presentar como no conformidades en la actividad realizada en la campaa y las personas responsables debern documentar el anlisis de la situacin, las acciones a realizar para evitar la situacin y el seguimiento de las recurrencias, con el fin de tomar acciones ms drsticas.

    .h5.mb-5  Estrategias asociadas a promocin de la donacin de sangre (INS, 2020)

    p.mb-5 Debido a que los productos sanguneos generan un alto impacto en la prestacin de los servicios de salud, en la calidad de vida de ciertos pacientes y en la salud pblica de un pas, <b> esta estrategia da a conocer la propuesta de algunas de las actividades que son permanentes y deben ser desarrolladas por cada actor que hace parte de la red de sangre,</b> con el fin de lograr los objetivos comunes: solidaridad, acceso, disponibilidad, y seguridad en el territorio nacional. 

    figure.mb-5
      img(src="@/assets/curso/images/pages/ilustraciones/img_21.png", alt="alt", style="width:1200px;")
    
    p.mb-4 Dentro de las acciones que debe realizar los promotores (bancos de sangre), se recomienda:

    .crd_A.crd_A--gris.brd-r1.mb-5
      
      .h6
        span.etiqueta--roja #[strong Informacin, sensibilizacin y educacin:]
      p.mt-4 Proyectar mensajes dirigidos a la comunidad en donde se fundamente la importancia de la donacin voluntaria y habitual de sangre, conductas de autocuidado, aclaracin de mitos y motivacin de autoexclusin como conducta solidaria.
      hr
      p Evaluar la factibilidad de realizar proyectos educativos sobre la donacin voluntaria y habitual de sangre de sangre, con aplicacin especial en nios y jvenes.
      hr        
      p Programar recursos (incluye econmicos y humanos) que permitan viabilizar la implementacin de estrategias para informar, sensibilizar y educar sobre la donacin voluntaria y habitual de sangre.
      hr
      p Programar proyectos de investigacin en donacin voluntaria y habitual de sangre.
      hr
      p.mb-5 Prever la organizacin de semilleros de promotores (voluntariado) en funcionarios empresariales, estudiantes de colegio y universidades.

      span.etiqueta--roja #[strong Da Mundial del Donante de Sangre:]
      p.mt-4 Identificar el tema y lema definido para la celebracin, por el nivel nacional (INS) y acorde al llamado internacional.
      hr
      p.mb-5 Definir el homenaje y tipo de reconocimiento social para donantes habituales y empresas que apoyan la donacin de sangre.
        
      span.etiqueta--roja #[strong Retroalimentacin de indicadores:]
      p.mt-4.mb-5 Programar jornadas de capacitacin para la interpretacin, anlisis y toma de decisin, sobre el comportamiento de los indicadores que impactan el proceso de promocin de la donacin voluntaria y habitual de sangre.

      span.etiqueta--roja #[strong Campaas masivas / Campaas de donacin de sangre peridicas:]
      p.mt-4 Programar capacitaciones, respecto a los aspectos tcnicos que se deben tener en cuenta en la planeacin y programacin de jornadas masivas y peridicas de donacin de sangre.
      hr
      p Priorizar la planeacin de jornadas de donacin en poblacin de bajo riesgo acorde con las necesidades de inventarios y demanda de hemocomponentes a satisfacer.
      hr
      p Contar con una base de datos que contenga la informacin de contacto de los donantes habituales y con grupos sanguneos de alta demanda.

    .h5.mt-5 Notificacin de tres casos de infeccin transmitida por transfusin - VIH

    p.mb-4 En el boletn del INS (2020) se da a conocer un caso de hemovigilancia presentado, y en el cual se infectaron de VIH a tres pacientes menores de edad, a travs de la transfusin de sangre, cuya unidad fue donada por un donante habitual.

    .row.mb-5
      .col-12.col-lg
        .h5.mt-2
          <b>El promotor siempre debe enfatizar en el tipo de informacin que se le manifiesta a las personas </b>
          span.etiqueta--green previamente a que realicen la donacin.
      .col-12.col-lg-8
        p.mt-2 Debido a que el donante de sangre habitual ha sido considerado como uno de los de menor riesgo de infeccin,<b> este tipo de casos ponen en evidencia que el promotor siempre debe enfatizar en el tipo de informacin que se le manifiesta a las personas previamente a que realicen la donacin,</b> no se puede olvidar que el ser informado adecuadamente es un derecho que tienen las personas, as como la decisin que tomen posteriormente al haberse enterado de los aspectos relacionados con la donacin de sangre (INS, 2020).

